/* form ::: checkbox */
.form_checkbox {
  display: inline-block;
  cursor: pointer;
  overflow: hidden;
  position: relative;
  vertical-align: middle;
  color: #292929;
  font-size: 16px;
  line-height: 26px;
  letter-spacing: -0.05em
}

.form_checkbox input[type="checkbox"] {
  height: 0;
  opacity: 0;
  width: 0;
  position: absolute;
  left: -10px;
}

.form_checkbox .icon {
  width: 26px;
  height: 26px;
  border: 2px solid #dddddd;
  border-radius: 50px;
  display: inline-block;
  position: relative;
  vertical-align: top;
  margin-right: 10px;
}

.form_checkbox .icon:before {
  content: '';
  width: 0;
  height: 20px;
  background: url(./image/header/icon_checkbox.png) 0 0 no-repeat;
  position: absolute;
  left: 4px;
  top: 0;
  transition-duration: 0.2s
}

.form_checkbox input[type="checkbox"]:checked+.icon:before {
  width: 24px;
}

.form_checkbox em {
  color: #009223;
}

/* form ::: file */
.form_file {
  border: 2px solid #dddddd;
  position: relative;
  display: inline-block;
  cursor: pointer;
  padding-right: 98px;
  box-sizing: border-box;
}

.form_file:before {
