}

.form_file:after {
  content: '파일선택';
  position: absolute;
  right: 20px;
  top: 0;
  z-index: 1;
  cursor: pointer;
  line-height: 41px;
  color: #009223;
  font-size: 16px;
  font-weight: 300;
  letter-spacing: -0.05em
}

.form_file input[type="file"] {
  position: absolute;
  right: 0;
  top: 0;
  height: 41px;
  width: 98px;
  text-indent: -9999px;
  opacity: 0;
  z-index: 3;
  cursor: pointer;
}

.form_file input[type="text"] {
  width: 100%;
  height: 41px;
  border: 0;
  display: block;
  text-indent: 13px;
}

