  color: #292929;
}

.card_info_wrap .gift_info_cont .info dl dt:before {
  content: '· ';
}

.card_info_wrap .gift_info_cont .info dl dd {
  font-size: 16px;
  color: #999999;
}

.card_info_wrap .gift_info_cont .info dl dd span {
  display: inline-block;
  position: relative;
  padding-left: 13px;
  margin-left: 14px
}

.card_info_wrap .gift_info_cont .info dl dd span:before {
  content: '';
  position: absolute;
  left: 0;
  top: 3px;
  width: 1px;
  height: 15px;
  background: #dddddd;
}

.card_info_wrap .gift_info_cont .info dl dd span:first-child {
  padding-left: 0;
  margin-left: 0;
}

.card_info_wrap .gift_info_cont .info dl dd span:first-child:before {
  display: none;
}

/* 카드 layout */
.card_img {
  position: relative;
  height: 318px;
  width: 498px;
}

.card_img .img {
  box-shadow: 6px 5px 7px rgba(0, 0, 0, 0.2);
  overflow: hidden;
  border-radius: 20px;
  height: 300px;
  width: 480px;
}

.card_img .img img {
  height: 100%;
  width: 100%;
}

.card_img .status {
  /* background: url(../images/common/icon_status_use.png); */
  display: block;
  text-align: center;
  position: absolute;
  left: 25px;
  top: 7px;
  height: 70px;
  width: 70px;
}

.card_img .status::before {
  content: '사용';
  color: #ffffff;
  font-size: 26px;
  font-style: normal;
  font-weight: bold;
  line-height: 70px;
}

/* 카드이름, 잔액 layout */
.mycard_total {
  overflow: hidden;
}

.mycard_total .name_edit {
  overflow: hidden;
}

.mycard_total .name_edit .form_text {
  background-color: #ffffff;
}

.mycard_total .name_edit .form_text input {
  color: #999999;
  font-family: 'font_sw';
  font-size: 20px;
  letter-spacing: -0.05em;
}

.mycard_total .txt_price {
  color: #292929;
  font-family: 'font_sw';
  font-size: 40px;
  font-weight: bold;
}

.mycard_total .txt_price:before {
  /* background: url(../images/common/icon_won_bk_28x17.png) no-repeat 0 0; */
  display: inline-block;
  content: '';
  line-height: 0;
  position: relative;
  top: -3px;
  margin-right: 8px;
  height: 17px;
  width: 28px;
}

/* bullet list */
.bullet_copy {
  overflow: hidden;
}

.bullet_copy li {
  overflow: hidden;
  color: #999999;
  line-height: 1.5em;
  position: relative;
  padding-left: 9px;
}

.bullet_copy li~li {
  margin-top: 11px;
}

.bullet_copy li:before {
  background-color: #999999;
  content: '';
  border-radius: 20px;
  position: absolute;
  left: 0;
  top: 9px;
  height: 4px;
  width: 4px;
}

/* 레이어팝업 */
.popup_iframe_wrapper {
  background-color: #ffc300;
  border-radius: 30px;
  box-sizing: border-box;
  padding: 20px 15px 15px 15px;
}

.popup_iframe_wrapper.no_wrapper {
  background-color: transparent;
  padding: 0;
}

#popup_wrap.new_style {
  background-color: transparent;
  border-radius: 0;
  padding: 0;
}

/* 레이어팝업 높이 작은화면 스크롤 영역 */
#popup_wrap .popup_content.content_scroll .scroll_wrap {
  height: auto !important;
}

#popup_wrap .popup_content.content_scroll .mCSB_inside>.mCSB_container {
  margin-right: 0;
}

#popup_wrap .popup_content.content_scroll .mCSB_scrollTools .mCSB_draggerContainer {
  top: 5px;
  height: 95%;
}

#popup_wrap.set_popup .popup_content.content_scroll {
  min-height: 258px !important;
  max-height: 276px !important;
  height: auto;
}

.hide {
  display: none;
}

/* --- table calendar --- 201800131*/
.date_calendar {
  display: inline-block;
  width: 196px;
  vertical-align: middle;
  overflow: hidden;
  position: relative;
  border: 2px solid #ddd;
  box-sizing: border-box;
}

.date_calendar:after {
  content: '';
  position: absolute;
  right: 48px;
  top: 13px;
  width: 2px;
  height: 16px;
  background-color: #e8e8e8;
}

.date_calendar img {
  cursor: pointer;
  position: relative;
}

.date_calendar input {
  position: relative;
  width: calc(100% - 48px);
  border: 0;
  height: 41px;
  line-height: 41px;
  font-size: 16px;
  color: #999;
  text-indent: 9px;
}

.date_calendar .input-group-btn {
  display: none;
  margin-left: -6px;
}

